import React, { Component } from 'react';
import './App.css';
import { Rosinante, Vision } from './Rosinante';

interface ImageTypes {

	changed: any;
	topLine: any;
	bottomLine: any;
	once: any;
	throttle: any;
	responsiveBreakPoint: any;
	EPTS: any;
	EPBS: any;
	EPTL: any;
	EPBL: any;
	index: any;
	picture: any;
  
};

interface ImageState {

	keyFrameSet: any;
	shape: any;
	Rosinante: any;
	changed: any;
  
};

class Image extends Component<ImageTypes, ImageState> {

	constantFrame: any;
	image: any;

	constructor(props: ImageTypes) {

		super(props);

		this.constantFrame = {

            		WebkitClipPath: `polygon(50% 0%, 100% 0, 100% 100%, 0 100%, 0 0)`,
            		ClipPath: `polygon(50% 0%, 100% 0, 100% 100%, 0 100%, 0 0)`,
            		WillChange: `clip-path`,
            		transition: `clip-path 500ms ease 0s; -webkit-clip-path 500ms ease 0s`

       	 	};
		
		this.state = {

            		keyFrameSet: false,
           		shape: {},
			Rosinante: false,
			changed: false,

            	}

		this.image = React.createRef();
		
		this.generateKeyframe = this.generateKeyframe.bind(this);

		this.itemVisible = this.itemVisible.bind(this);
		this.itemNotVisible = this.itemNotVisible.bind(this);

		this.getRandomNumber = this.getRandomNumber.bind(this);
		

	}

	componentWillUnmount() {

		this.state.Rosinante.removeRosinante();

	}

	itemVisible() {	

		  this.image.current.style.webkitClipPath = this.constantFrame.WebkitClipPath;
		  this.setState({ keyFrameSet: false, changed: false });

		

	}

	itemNotVisible() {

		if (!this.state.keyFrameSet) {

			let newShape = this.generateKeyframe();

			this.image.current.style.webkitClipPath = newShape.WebkitClipPath;
			this.setState({ keyFrameSet: true });

		}

	}

	componentDidUpdate(prevProps: any, prevState: any) {

		if (this.state.changed !== prevState.changed) {

			this.state.Rosinante.callRosinante();

		}

		if (this.state.Rosinante.Rosinante && prevState.Rosinante.Rosinante) {

			if ((this.state.Rosinante.Rosinante.once !== prevState.Rosinante.Rosinante.once  || this.state.Rosinante.Rosinante.throttle !== prevState.Rosinante.Rosinante.throttle || this.state.Rosinante.Rosinante.responsiveBreaKPoint !== prevState.Rosinante.Rosinante.responsiveBreakPoint || this.state.Rosinante.Rosinante.elementPieceTopSmall !== prevState.Rosinante.Rosinante.elementPieceTopSmall || this.state.Rosinante.Rosinante.elementPieceBottomSmall !== prevState.Rosinante.Rosinante.elementPieceBottomSmall || this.state.Rosinante.Rosinante.elementPieceTopLarge !== prevState.Rosinante.Rosinante.elementPieceTopLarge || this.state.Rosinante.Rosinante.elementPieceBottomLarge !== prevState.Rosinante.Rosinante.elementPieceBottomLarge) && this.state.changed) {

				prevState.Rosinante.removeRosinante();
				this.state.Rosinante.callRosinante();

			}

		}

		if (prevProps.changed !== this.props.changed) {
		
			if (this.props.changed && this.props.topLine && this.props.bottomLine) {

				this.setState({ shape: this.constantFrame, Rosinante: new Rosinante({

					scrollContainer: window, // Scroll Container: this.scrollableContainer.current
					topLine: this.props.topLine.current, // Top Line
					bottomLine: this.props.bottomLine.current, // Bottom Line
					scrollItem: this.image.current, // Scroll Item
					scrollTarget: this.image.current, // Scroll Target
					once: this.props.once, // Once
					visible: this.itemVisible, // Visible
					notVisible: this.itemNotVisible, // Not Visible
					throttle: this.props.throttle, // Throttle Value
					responsiveBreakPoint: this.props.responsiveBreakPoint, // under 800 small screen, over 800 larger screen
					elementPieceTopSmall: this.props.EPTS, // Element Piece Top Small
					elementPieceBottomSmall: this.props.EPBS, // Element Piece Bottom Small
					elementPieceTopLarge: this.props.EPTL, // Element Piece Top Large
					elementPieceBottomLarge: this.props.EPBL, // Element Piece Bottom Large

				}), changed: true});

			}

		}



	}
	
	componentDidMount() {

		

  	}
	
	generateKeyframe() {

        	let point1x = 50;
        	let point1y = Math.floor(this.getRandomNumber(0, 40));
        	let point2x = Math.floor(this.getRandomNumber(60, 100));
        	let point2y = Math.floor(this.getRandomNumber(0, 40));
        	let point3x = Math.floor(this.getRandomNumber(60, 100));
        	let point3y = Math.floor(this.getRandomNumber(60, 100));
        	let point4x = Math.floor(this.getRandomNumber(0, 40));
        	let point4y = Math.floor(this.getRandomNumber(60, 100));
        	let point5x = Math.floor(this.getRandomNumber(0, 40));
        	let point5y = Math.floor(this.getRandomNumber(0, 40));
        
        	let keyFrames = {

            		WebkitClipPath: `polygon(${point1x}% ${point1y}%, ${point2x}% ${point2y}%, ${point3x}% ${point3y}%, ${point4x}% ${point4y}%, ${point5x}% ${point5y}%)`,
            		ClipPath: `polygon(${point1x}% ${point1y}%, ${point2x}% ${point2y}%, ${point3x}% ${point3y}%, ${point4x}% ${point4y}%, ${point5x}% ${point5y}%)`,
            		
        		};

    		return keyFrames;
    
    	};

	
    	getRandomNumber(min: any, max: any) {

    		return Math.random() * (max - min) + min;
        
    	};

	render() {

		let first = <img alt="alt" ref={this.image} className="imageTransition" src={this.props.picture} />;
		let second = <div></div>;

		if (this.props.index % 2 === 0 ) {

			first = <div></div>;
			second = <img alt="img" ref={this.image} className="imageTransition" src={this.props.picture} />;
			

		}

		return ( 

			<div className="scrollTest">
          			{first}
	  			{second}
			</div>
	
			);
	
	}

}

interface PresentationTypes {


  
};

interface PresentationState {

	pictures: any;
	changed: any;
	form: any;
	current: any;

  
};

class Presentation extends Component<PresentationTypes, PresentationState> {

	ref: any;

        constructor(props: PresentationTypes) {

		super(props);

		this.state = { 

			pictures: ["/rosinante/0.jpeg", "/rosinante/1.jpeg", "/rosinante/2.jpeg", "/rosinante/3.jpeg", "/rosinante/4.jpeg" ], 
			changed: false, 
			form: { bottomLine: 20, topLine: 78, once: false, visible: true,  throttle: 400, responsiveBreakPoint: 800, EPTS: 140, EPBS: 40, EPTL: 350, EPBL: 100  },
			current: false
			
			};


		this.change = this.change.bind(this);
		this.ref = React.createRef();
            
        };

	componentDidUpdate() {
			


	}

	change(e: any) {

		let target = e.target;

		if (target.name === "once") {

			let value = target.checked;

			this.setState({ changed: true, form: { ...this.state.form, once: value } });

		}

		if (target.name === "visible") {

			let value = target.checked;

			this.setState({ changed: true, form: { ...this.state.form, visible: value } });

		}

		if (target.name === "throttle") {

			let value = parseInt(target.value);

			this.setState({ changed: true, form: { ...this.state.form, throttle: value } });

		}

		if (target.name === "responsiveBreakPoint") {

			let value = parseInt(target.value);

			this.setState({ changed: true, form: { ...this.state.form, responsiveBreakPoint: value } });

		}

		if (target.name === "EPTS") {

			let value = parseInt(target.value);

			this.setState({ changed: true, form: { ...this.state.form, EPTS: value } });

		}

		if (target.name === "EPBS") {

			let value = parseInt(target.value);

			this.setState({ changed: true, form: { ...this.state.form, EPBS: value } });

		}

		if (target.name === "EPTL") {

			let value = parseInt(target.value);

			this.setState({ changed: true, form: { ...this.state.form, EPTL: value } });

		}

		if (target.name === "EPBL") {

			let value = parseInt(target.value);

			this.setState({ changed: true, form: { ...this.state.form, EPBL: value } });

		}

		if (target.name === "topLine") {

			let value = parseInt(target.value);
	
			if (target.value > 100) {

				value = 100;

			}

			if (target.value < 50) {

				value = 50;

			}

			this.setState({ form: { ...this.state.form, topLine: value } });


		}

		if (target.name === "bottomLine") {

			let value = parseInt(target.value);
	
			if (target.value > 50) {

				value = 50;

			}

			if (target.value < 0) {

				value = 0;

			}

			this.setState({ form: { ...this.state.form, bottomLine: value } });

		}

		
		setTimeout(() => {

			this.setState({ changed: false });

		}, 1);
	
	}

	componentDidMount() {

		if (this.ref.current) {

			this.setState({ current: this.ref.current, changed: true });

			setTimeout(() => {

				this.setState({ changed: false });

			}, 1);

		}

	}

  	render() {

		let vision = this.state.current;
		let topLine: any;
		let bottomLine: any;

		if (vision) {

			topLine = vision.topLine;
			bottomLine = vision.bottomLine;

		}

		return (
      			<React.Fragment>
				<Vision ref={this.ref} vision={this.state.form.visible} container={window} top={this.state.form.topLine} bottom={this.state.form.bottomLine} />
				
					<div className="rosinante-form-explaination">
					<form className="rosinante-form">
						<label>Top Line</label>
						<input onKeyUp={this.change} type="text" name="topLine" defaultValue={this.state.form.topLine} />
						<label>Bottom Line</label>
						<input onKeyUp={this.change} type="text" name="bottomLine" defaultValue={this.state.form.bottomLine}/>
						<label>Fire Only Once</label>
						<input onChange={this.change} type="checkbox" name="once"/>
						<label>Lines Visible</label>
						<input onChange={this.change} checked={this.state.form.visible} type="checkbox" name="visible"/>
						<label>Throttle</label>
						<input onKeyUp={this.change} type="text" name="throttle" defaultValue={this.state.form.throttle}/>
						<label>Responsive Break Point</label>
						<input type="text" name="responsiveBreakPoint" defaultValue={this.state.form.responsiveBreakPoint} />
						<label>{`Element Piece = 0 (top of the element)`}</label>
						<label>Element Piece Top (Small Screen)</label>
						<input onKeyUp={this.change} type="text" name="EPTS" defaultValue={this.state.form.EPTS} />
						<label>Element Piece Bottom (Small Screen)</label>
						<input onKeyUp={this.change} type="text" name="EPBS" defaultValue={this.state.form.EPBS}/>
						<label>Element Piece Top (Large Screen)</label>
						<input onKeyUp={this.change} type="text" name="EPTL" defaultValue={this.state.form.EPTL}/>
						<label>Element Piece Bottom (Large Screen)</label>
						<input onKeyUp={this.change} type="text" name="EPBL" defaultValue={this.state.form.EPBL}/>
					</form>
					<div className="rosinante-info"> </div>
					</div>
				{this.state.pictures.map((picture: any, index: number) => {

				return <Image once={this.state.form.once} throttle={this.state.form.throttle} responsiveBreakPoint={this.state.form.responsiveBreakPoint} EPTS={this.state.form.EPTS} EPBS={this.state.form.EPBS} EPTL={this.state.form.EPTL} EPBL={this.state.form.EPBL} changed={this.state.changed} topLine={topLine} bottomLine={bottomLine}  picture={picture} key={picture} index={index}/>;

				})}
      			</React.Fragment>
    			);

  		}

}

export default Presentation;
