import React, { Component } from 'react';
import './App.css';


const text1 = `The library is divided into two parts, one is a React Component (called Vision) that returns two reference lines and the other is the main part of the library (called Rosinante), that takes care of the basic functions needed for the whole system to work. The reason for the decoupling (at the beginning, they were tightly bound together) is to allow for some freedom and to make it possible to pass a reference of the same two lines to multiple components. That way the lines won't be rendered multiple times, depending on how much components are on the screen. The lines are the result of my frustration with the lack of visualisation in the conventional way of doing this. You guessed it, I wrote a library to avoid having to type in some numbers and not having any visual representation on the screen. I could have learned it, mastered the system, but where is the fun in that?`

const text2 = `Disclaimer: if you are looking for a library that has some built in fancy animations and cool effects that will save you time and probably money, I need to disappoint you, that's not the problem we are solving here.`;

const text3 = `Like already demonstrated, the Vision component takes in a couple of props. One of them is the "vision" prop, which is supposed to be of boolean type and, if set to true, the lines will be visible (for working purposes), if set to false, the lines will get an opacity of zero and a z-index of -999999999999, which brings us to another prop. The zIndex prop will allow you to change the z-index according to your needs. It works only if visible is true and is relly optional. The container prop lets you set the container. If the container is of type Window, the lines will get the position "fixed", if it's an element, they'll get the position sticky (but watch out with this one if you are aiming for browser compatibility). The last two props, top and bottom, are required. The "top" prop accepts values between 100 and 50, if you type in a value that is below 50 and above 100, it will lock you down to the maximum allowed value. The "bottom" prop accepts values between 0 and 50. Important thing to note is, that the values 0 to 100 represent the percentage of the screen/container.`;

const text4 = `Now comes the fun part. If you create a ref for the "Vision" component, you will be granted access to the two lines, which you can pass down, in form of props, to whichever component you desire.`;

const text5 = `It's time to call the horse.`;

const first = `The "Rosinante" class takes quite a lot of in. The first parameter is the container, to which we will apply the scroll event listener. It can be a div or the window.`;

const second = `The second and third parameter are the lines, that we have passed in the form of props to the component we want to change.`;

const third = `The fourth parameter is the item that gets affected by the scroll event. The fifth parameter, can be the same item, but it can also be another target if the item is currently off screen due to your setup.`;

const fourth = `The sixth parameter defines whether or not you want the event to fire once or multiple times. The seventh and eight parameter are two function, that you will code yourself and pass to the class. One function defines the look of your item when it's detected to be between the two lines (on screen), the other function defines the look of your item when it's off screen. I think you can set one of them to false, because the function that handles them checks whether or not the argument is a function or not. But you shouldn't skip one entirely.`;

const fifth = `The ninth parameter is the throttle value. The class uses the "lodash.throttle/debounce" functions, so don't worry, I didn't code that one myself.`;

const sixth = `The tenth parameter is the width value, which breaks down the application into two parts. Why? It's explained in the next paragraph.`;

const seventh = `The last four parameters define the element pieces. The simplest explanation is the following. If you set the element piece to zero, it means that the event will trigger when the top or bottom line (EPT/EPB) passes the top point of the element. It's prefered that the value for Element Piece Bottom is smaller than the value for Element Piece Top, because you are defining when the element is on/off screen.`;

const eight = `That's all I have for now. You can test the library in the preview section or get the file, it's in the GitHub repository of this page on my profile. If you have any constructive or unconstructive criticism, do not hold back! Have a nice day and don't forget to live the code!`;

