import React, { Component } from 'react';
import './App.css';
import Presentation from './Presentation';
import Docs from './Docs';
import { Link } from 'react-router-dom';
import { Helmet } from "react-helmet";

interface AppTypes {

	

};

interface AppState {

	loading: any;
	location: any;
  
};

class RosComponent extends Component<AppTypes, AppState> {

        constructor(props: AppTypes) {

		super(props);
		this.state = { loading: true, location: "home" }
		
            
        };

	componentDidUpdate() {
			


	}

	componentDidMount() {

		setTimeout(() => { this.setState({ loading: false }); }, 1);
		
	}

  	render() {

		let loading: any;
		let content: any;
		let home: any;
		let docs: any;
		let component: any;

		if (this.state.location === "home") {
		 
			home = <Link className="active-link" to="/">Home</Link>;
			docs = <Link to="/docs"  onClick={((e: any)=>{ e.preventDefault(); this.setState({ ...this.state, location: "docs" }); })}>Docs</Link>;


		}

		if (this.state.location === "docs") {
		
			home = <Link to="/" onClick={((e: any)=>{ e.preventDefault(); this.setState({ ...this.state, location: "home" }); })}>Home</Link>;
			docs = <Link className="active-link" to="/docs">Docs</Link>

		}

		if (this.state.loading) {

			loading = <div className="loadingScreen"></div>;

		}

		if (!this.state.loading) {

			if (this.state.location === "home") {

				component = <Presentation />;

			}

			if (this.state.location === "docs") {

				component = <Docs />;

			}

			content = <div className="App">
	
				<div className="rosinante-presentation">
					<div className="rosinante-name">
						<div>Rosinante</div>
						<div style={{ fontSize: "1rem" }}>Scroll Event Library for React.js</div>
						<span>
							<a style={{ textDecoration: "none", color: "white" }} title="https://github.com/rosinant3" href="https://github.com/rosinant3/rosinant3.github.io/tree/master/Rosinante" target="_blank" rel="noopener noreferrer" className="github">/rosinant3.github.io/tree/master/Rosinante</a>
						</span>
					</div>
				</div>

				<nav>
				{home}
				{docs}
				</nav>
					{component}
      			</div>;

		}

		return (<React.Fragment>
					<Helmet>
            		</Helmet>
					{loading}
					{content}
				</React.Fragment>);

  		}

}

export default RosComponent;
